@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800;900&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  body {
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%);
  }

  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }

  /* Smooth scrolling */
  html {
    scroll-behavior: smooth;
  }

  /* Custom scrollbar */
  ::-webkit-scrollbar {
    width: 8px;
  }

  ::-webkit-scrollbar-track {
    background: #f1f5f9;
  }

  ::-webkit-scrollbar-thumb {
    background: #cbd5e1;
    border-radius: 4px;
  }

  ::-webkit-scrollbar-thumb:hover {
    background: #94a3b8;
  }
}

@layer components {
  /* Enhanced Buttons with Medical Theme */
  .btn-primary {
    @apply bg-gradient-to-r from-blue-600 to-indigo-600 hover:from-blue-700 hover:to-indigo-700 
           text-white font-semibold py-3 px-6 rounded-xl shadow-lg hover:shadow-xl 
           transition-all duration-300 transform hover:-translate-y-0.5 
           focus:outline-none focus:ring-4 focus:ring-blue-300 focus:ring-opacity-50;
  }
  
  .btn-secondary {
    @apply bg-gradient-to-r from-gray-100 to-gray-200 hover:from-gray-200 hover:to-gray-300 
           text-gray-700 font-semibold py-3 px-6 rounded-xl shadow-md hover:shadow-lg 
           transition-all duration-300 transform hover:-translate-y-0.5 
           border border-gray-300 hover:border-gray-400;
  }
  
  .btn-success {
    @apply bg-gradient-to-r from-emerald-500 to-green-600 hover:from-emerald-600 hover:to-green-700 
           text-white font-semibold py-3 px-6 rounded-xl shadow-lg hover:shadow-xl 
           transition-all duration-300 transform hover:-translate-y-0.5;
  }
  
  .btn-warning {
    @apply bg-gradient-to-r from-amber-500 to-orange-500 hover:from-amber-600 hover:to-orange-600 
           text-white font-semibold py-3 px-6 rounded-xl shadow-lg hover:shadow-xl 
           transition-all duration-300 transform hover:-translate-y-0.5;
  }
  
  .btn-danger {
    @apply bg-gradient-to-r from-red-500 to-rose-600 hover:from-red-600 hover:to-rose-700 
           text-white font-semibold py-3 px-6 rounded-xl shadow-lg hover:shadow-xl 
           transition-all duration-300 transform hover:-translate-y-0.5;
  }
  
  /* Enhanced Cards with Medical Aesthetic */
  .card {
    @apply bg-white bg-opacity-80 backdrop-blur-sm rounded-2xl shadow-xl p-8 border border-white border-opacity-20 
           hover:shadow-2xl transition-all duration-300 transform hover:-translate-y-1 
           hover:bg-white hover:bg-opacity-90;
  }

  .medical-card {
    @apply bg-gradient-to-br from-white to-blue-50 rounded-2xl shadow-xl p-6 
           border border-blue-100 hover:shadow-2xl transition-all duration-300 
           transform hover:-translate-y-1 hover:border-blue-200;
  }

  .stat-card {
    @apply bg-gradient-to-br from-white to-blue-50 
           rounded-2xl shadow-lg p-6 border border-blue-100 
           hover:shadow-xl transition-all duration-300 transform hover:scale-105 
           hover:border-blue-200 cursor-pointer;
  }
  
  /* Enhanced Forms */
  .form-group {
    @apply mb-6;
  }
  
  .form-label {
    @apply block text-sm font-semibold text-gray-700 mb-3 tracking-wide;
  }
  
  .form-input {
    @apply w-full px-4 py-3 border-2 border-gray-200 rounded-xl shadow-sm 
           focus:outline-none focus:ring-4 focus:ring-blue-200 focus:border-blue-400 
           transition-all duration-200 bg-white bg-opacity-80 backdrop-blur-sm
           hover:border-gray-300 hover:shadow-md;
  }

  .form-input:focus {
    @apply bg-white;
  }
  
  /* Enhanced Alerts with Medical Colors */
  .alert {
    @apply p-6 rounded-2xl mb-6 border-l-4 shadow-lg backdrop-blur-sm;
  }
  
  .alert-success {
    @apply bg-gradient-to-r from-emerald-50 to-green-50 border-l-emerald-500 
           text-emerald-800 shadow-emerald-100;
  }
  
  .alert-warning {
    @apply bg-gradient-to-r from-amber-50 to-yellow-50 border-l-amber-500 
           text-amber-800 shadow-amber-100;
  }
  
  .alert-danger {
    @apply bg-gradient-to-r from-red-50 to-rose-50 border-l-red-500 
           text-red-800 shadow-red-100;
  }
  
  .alert-info {
    @apply bg-gradient-to-r from-blue-50 to-cyan-50 border-l-blue-500 
           text-blue-800 shadow-blue-100;
  }
  
  /* Enhanced Badges */
  .badge {
    @apply inline-flex items-center px-3 py-1.5 rounded-full text-xs font-semibold 
           shadow-sm ring-1 ring-inset;
  }
  
  .badge-success {
    @apply bg-gradient-to-r from-emerald-100 to-green-100 text-emerald-700 
           ring-emerald-600/20;
  }
  
  .badge-warning {
    @apply bg-gradient-to-r from-amber-100 to-yellow-100 text-amber-700 
           ring-amber-600/20;
  }
  
  .badge-danger {
    @apply bg-gradient-to-r from-red-100 to-rose-100 text-red-700 
           ring-red-600/20;
  }
  
  .badge-info {
    @apply bg-gradient-to-r from-blue-100 to-cyan-100 text-blue-700 
           ring-blue-600/20;
  }

  /* Loading Animation */
  .loading-pulse {
    animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
  }

  /* Floating Animation for Hero Elements */
  .float-animation {
    animation: float 6s ease-in-out infinite;
  }

  @keyframes float {
    0%, 100% { transform: translateY(0px); }
    50% { transform: translateY(-10px); }
  }

  /* Glow Effect for Important Elements */
  .glow-effect {
    box-shadow: 0 0 20px rgba(59, 130, 246, 0.3);
  }

  /* Medical Cross Pattern Background */
  .medical-pattern {
    background-image: 
      linear-gradient(45deg, rgba(59, 130, 246, 0.03) 25%, transparent 25%),
      linear-gradient(-45deg, rgba(59, 130, 246, 0.03) 25%, transparent 25%),
      linear-gradient(45deg, transparent 75%, rgba(59, 130, 246, 0.03) 75%),
      linear-gradient(-45deg, transparent 75%, rgba(59, 130, 246, 0.03) 75%);
    background-size: 30px 30px;
    background-position: 0 0, 0 15px, 15px -15px, -15px 0px;
  }

  /* Glass Morphism Effect */
  .glass {
    @apply bg-white bg-opacity-25 backdrop-blur border border-white border-opacity-20;
  }
} 